<template>
    <v-layout fill-height column align-center justify-center>
      <div class="listFrame animate__animated animate__lightSpeedInLeft" style="gap:40px;">
        <div class="card" @mousemove="rotateToMouse($event,0)" @mouseleave="stopRotate(0)">
          <div class="card_title">Frontend</div>
          <div class="d-flex align-center">
            <img src="@/assets/html.png" alt="">
            <div class="card_items">HTML,CSS,JS</div>
          </div>
          <div class="d-flex align-center">
            <img src="@/assets/sass.png" alt="">
            <div class="card_items">SASS</div>
          </div>
          <div class="d-flex align-center">
            <img src="https://avatars.githubusercontent.com/u/6128107?s=200&v=4" alt="">
            <div class="card_items">VUE.JS</div>
          </div>
          <div class="d-flex align-center">
            <img src="@/assets/vuetify.png" alt="">
            <div class="card_items">VUETIFY</div>
          </div>
          <div class="glow"/>
        </div>
        <div class="card" @mousemove="rotateToMouse($event,1)" @mouseleave="stopRotate(1)">
          <div class="card_title">Version Control</div>
          <div class="d-flex align-center">
            <img src="@/assets/github.png" alt="">
            <div class="card_items">GIT HUB</div>
          </div>
          <div class="d-flex align-center">
            <img src="@/assets/git.png" alt="">
            <div class="card_items">GIT</div>
          </div>
          <div class="glow"/>
        </div>
        <div class="card" @mousemove="rotateToMouse($event,2)" @mouseleave="stopRotate(2)">
          <div class="card_title">Communication</div>
          <div class="d-flex align-center">
            <img src="@/assets/figma.png" alt="">
            <div class="card_items">FIGMA</div>
          </div>
          <div class="d-flex align-center">
            <img src="@/assets/zeplin.png" alt="">
            <div class="card_items">ZEPLIN</div>
          </div>
          <div class="glow"/>
        </div>
      </div>
    </v-layout>
</template>

<script>
export default {
    components: {
    },
    props: {},
    data() {
        return {
        };
    },
    watch: {
    },
    created() {},
    mounted() {
    },
    destroyed() {},
    methods: {
      stopRotate(num){
        let div = document.getElementsByClassName('card')[num];
        let glow = document.getElementsByClassName('glow')[num];
        div.style.transform = '';
        div.style.background = '';
        ;
        glow.style.backgroundImage = 'radial-gradient(circle at 50% -20%, #ffffff22, #0000000f)';
      },
      rotateToMouse(e,num){
        let div = document.getElementsByClassName('card')[num];
        let bounds = div.getBoundingClientRect();
        const mouseX = e.clientX;
        const mouseY = e.clientY;
        const leftX = mouseX - bounds.x;
        const topY = mouseY - bounds.y;
        const center = {
          x: leftX - bounds.width / 2,
          y: topY - bounds.height / 2
        }
        const distance = Math.sqrt(center.x**2 + center.y**2);

        
        div.style.transform = `
          scale3d(1.07, 1.07, 1.07)
          rotate3d(
            ${center.y / 100},
            ${-center.x / 100},
            0,
            ${Math.log(distance)* 2}deg
          )
        `;
        
        div.querySelector('.glow').style.backgroundImage = `
          radial-gradient(
            circle at
            ${center.x * 2 + bounds.width/2}px
            ${center.y * 2 + bounds.height/2}px,
            #ffffff55,
            #0000000f
          )
        `;
      }
    },
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/app.scss";
.card .glow {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-image: radial-gradient(circle at 50% -20%, #ffffff22, #0000000f);
  border-radius: 16px;
}
.card:hover {
  transition-duration: 150ms;
  box-shadow: 0 5px 20px 5px #00000044;
}
.card{
  box-shadow: 0 1px 5px #00000099;
  overflow: hidden;
  position: relative;
  border-radius: 16px;
  padding: 16px 16px 24px 16px;
  transition-duration: 300ms;
  transition-property: transform, box-shadow;
  transition-timing-function: ease-out;
  transform: rotate3d(0);
  div+:not(:first-child){
    margin-top: 20px;
  }
  div+:last-child{
    margin-top: 0px;
  }
  img{
    width: 100px;
    height: 100px;
    align-items: center;
    object-fit: scale-down;
  }
  &_title{
    font-family: Tenada;
    font-size: 20px;
    border-bottom: 1px solid #f5f5f5;
    padding-bottom: 12px;
  }
  &_items{
    font-family: Tenada;
    font-size: 20px;
    padding-left: 40px;
  }
}
.listFrame{
  margin-top: auto;
  display: flex;
  justify-content: center;
  margin: auto;
  align-items: start;
}
</style>